/*BUTTONS*/
.btn {
  display: inline-block;
  padding: 0 30px;
  height: 56px;
  line-height: 56px;
  overflow: hidden;
  background-color: #007397;
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;

  &:after {
    content: '';
    width: 0;
    position: absolute;
    @include anim(0.8s,0s);
    height: 100%;
    left: 50%;
    top: 0;
    background-color: #00526b;
  }

  span {
    position: relative;
    z-index: 2;
  }

  &:hover {

    &:after {
      left: 0;
      width: 100%;
    }
  }
}